.dashboard-popup-classname {
  background-color: #ffffff;
  border-radius: 7px;
  width: 360px;
  height: 280px;
  transform: translateY(230px);
  padding-top: 20px;
}

.dashboard-popup-classname p {
  font: 25px Chivo, sans-serif;
  text-align: center;
}

.dashboard-popup-classname label {
  font: 16px Overpass, sans-serif;
  margin-left: 20px;
}

.dashboard-popup-classname span {
  font: 16px Overpass, sans-serif;
  margin-left: 12px;
}

#dashboard-nickname-label {
  margin-top: 10px;
}

.dashboard-popup-classname input {
  font: 18px Overpass, sans-serif;
  margin-left: 20px;
  padding: 5px;
  width: 320px;
  border-radius: 3px;
  border: 2px solid #b3b3b3;
  box-sizing: border-box;
  transition: border-color 450ms;
}

.dashboard-popup-classname input:focus {
  outline: none;
  border-color: #01ccc0;
}
.dashboard-popup-classname button {
  font: 20px Overpass, sans-serif;
  margin-top: 14px;
  border-radius: 10px;
  height: 30px;
  cursor: pointer;
  transition-duration: 300ms;
}

.dashboard-popup-classname button:focus {
  outline: none;
}

.dashboard-popup-classname-save {
  width: 70px;
  background-color: #01ccc0;
  color: #ffff;
  border: none;
  margin-left: 25px;
}

.dashboard-popup-classname-save:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}

.dashboard-popup-classname-cancel {
  width: 80px;
  border: 1px solid #9fd0eb;
  color: #808080;
  background-color: #ffff;
  margin-left: 15px;
}

.dashboard-popup-classname-cancel:hover {
  background-color: #9fd0eb;
  color: #ffff;
}

.dashboard-popup-delete-classroom {
  background-color: #ffff;
  border-radius: 7px;
  width: 470px;
  height: 220px;
  transform: translateY(250px);
  padding-top: 20px;
  text-align: center;
}

.dashboard-popup-delete-classroom p {
  font: 25px Chivo, sans-serif;
}

.dashboard-popup-delete-classroom-warning {
  font: 20px Overpass, sans-serif;
  color: #e60000;
}

.dashboard-popup-delete-classroom button {
  font: 20px Overpass, sans-serif;
  height: 30px;
  margin-top: 15px;
  border-radius: 10px;
  cursor: pointer;
  transition-duration: 300ms;
}

.dashboard-popup-delete-classroom button:focus {
  outline: none;
}

.dashboard-popup-delete-classroom-delete {
  margin-right: 20px;
  width: 80px;
  background-color: #ffff;
  border: 1px solid #e60000;
  cursor: pointer;
}

.dashboard-popup-delete-classroom-delete:hover {
  color: #ffff;
  background-color: #e60000;
}

.dashboard-popup-delete-classroom-cancel {
  width: 80px;
  border: none;
  color: white;
  background-color: #01ccc0;
}

.dashboard-popup-delete-classroom-cancel:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}


